import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			orderId: 0,
			orderInfo: {},
			orderGoods: [],
			expressInfo: {},
			flag: false,
			handleOption: {},
		}
	},
	onLoad(options){
		this.orderId = options.id;
		this.getOrderDetail();
	},
	onPullDownRefresh() {
		this.getOrderDetail(true);
    },
	methods: {
		gobuy (item) {
			uni.redirectTo({
			  url: '/pages/product/product?goodsId='+item.goodsId
			});
		},
		gocomment (item) {
			console.log(item)
			uni.redirectTo({
			  url: '/pages/order/comment?orderId='+item.orderId+'&type='+item.type+'&valueId='+item.goodsId
			});
		},
		  expandDetail: function() {
			this.flag = !this.flag
		  },
		  getOrderDetail(flag) {
		    uni.showLoading({title: '加载中'});
		    let that = this;
			let orderurl = '/order/detail?orderId='+ this.orderId
			api.httpget(orderurl).then((res) => {
				let respons = res.data
				if (respons.errno == 0) {
					  this.orderInfo = respons.data.orderInfo
					  this.orderGoods = respons.data.orderGoods
					  this.handleOption = respons.data.orderInfo.handleOption
					  // this.expressInfo = respons.data.expressInfo
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				if(flag) uni.stopPullDownRefresh() //停止下拉刷新
				uni.hideLoading();
			})
		  },
		  // “去付款”按钮点击效果
		  payOrder: function() {
		    let that = this;
			let payurl = '/order/prepay'



			api.httppost(payurl, {
			  orderId:  that.orderId,
			  code: uni.getStorageSync('code')
		    }).then(function(res) {
		      if (res.data.errno === 0) {
		        const payParam = res.data.data;
		        console.log("支付过程开始");
		        uni.requestPayment({
		          'timeStamp': payParam.timeStamp,
		          'nonceStr': payParam.nonceStr,
		          'package': payParam.packageValue,
		          'signType': payParam.signType,
		          'paySign': payParam.paySign,
		          'success': function(res) {
					uni.showToast({title: '支付成功', icon: 'none'})
					that.$api.prePage().loadData(0);
					uni.navigateBack()
		          },
		          'fail': function(res) {
					  uni.showToast({title: '支付失败', icon: 'none'})
		          }
		        });
		      } else {
				  uni.showToast({title: res.data.errmsg, icon: 'none'})
			  }
		    })
		  },
		  // “取消订单”点击效果
		  cancelOrder: function() {
		    let that = this;
		    uni.showModal({
		      title: '',
		      content: '确定要取消此订单？',
		      success: function(res) {
		        if (res.confirm) {
					let orderurl = '/order/cancel'
					uni.showLoading({title: '正在加载'})
					api.httppost(orderurl, {orderId: that.orderId}).then((res) => {
						let respons = res.data
						if (respons.errno == 0) {
							  uni.showToast({
								title: '取消订单成功'
							  });
							  that.$api.prePage().loadData(0);
							  uni.navigateBack()
						} else {
							uni.showToast({title: respons.errmsg, icon: 'none'})
						}
						uni.hideLoading();
					})
		        }
		      }
		    });
		  },
		  // “取消订单并退款”点击效果
		  refundOrder: function() {
		    let that = this;
		    uni.showModal({
		      title: '',
		      content: '确定要退款此订单吗？',
		      success: function(res) {
				  if (res.confirm) {
					let orderurl = '/order/refund'
					uni.showLoading({title: '正在加载'})
					api.httppost(orderurl, {orderId: that.orderId}).then((res) => {
						let respons = res.data
						if (respons.errno == 0) {
							  uni.showToast({
								title: '已申请退款中！'
							  })
							  that.$api.prePage().loadData(0);
							  uni.navigateBack()
						} else {
							uni.showToast({title: respons.errmsg, icon: 'none'})
						}
						uni.hideLoading();
					})
				  }
		      }
		    });
		  },
		  // “删除”点击效果
		  deleteOrder: function() {
			let that = this;
			uni.showModal({
			  title: '',
			  content: '确定要删除此订单？',
			  success: function(res) {
				if (res.confirm) {
					let orderurl = '/order/delete'
					uni.showLoading({title: '正在加载'})
					api.httppost(orderurl, {orderId: that.orderId}).then((res) => {
						let respons = res.data
						if (respons.errno == 0) {
							  uni.showToast({
								title: '删除订单成功'
							  });
							  that.$api.prePage().loadData(0);
							  uni.navigateBack()
						} else {
							uni.showToast({title: respons.errmsg, icon: 'none'})
						}
						uni.hideLoading();
					})
				}
			  }
			});
		  },
		  // “确认收货”点击效果
		  confirmOrder: function() {
			let that = this;
			uni.showModal({
			  title: '',
			  content: '确认收货？',
			  success: function(res) {
				  if (res.confirm) {
					let orderurl = '/order/confirm'
					uni.showLoading({title: '正在加载'})
					api.httppost(orderurl, {orderId: that.orderId}).then((res) => {
						let respons = res.data
						if (respons.errno == 0) {
							  uni.showToast({
								title: '确认收货成功'
							  });
							  that.$api.prePage().loadData(0);
							  uni.navigateBack()
						} else {
							uni.showToast({title: respons.errmsg, icon: 'none'})
						}
						uni.hideLoading();
					})
				  }
			  }
			});
		}
	}
}
